@import "vars";

%Poppinsfont {
  font-family: Poppins, Arial, Helvetica, sans-serif;
}

.approach {
  display: flex;
  position: relative;

  height: auto;
  margin-left: 100px;

  background: $gray;
  border-radius: 30px 0 0 30px;
  transform: translateY(140px);

  &__content {
    display: flex;
    flex-direction: column;

    margin-left: 100px;
    width: 460px;

    color: #fff;
  }

  &__subheading {
    @extend %Poppinsfont;
    font-weight: bold;
    font-size: 13px;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__heading {
    @extend %Poppinsfont;
    margin-top: 0;
    font-weight: 600;
    font-size: 32px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    max-width: 500px;
  }

  &__item {
    position: relative;
    display: flex;
    height: auto;
    margin-bottom: 50px;

    &::after {
      position: absolute;
      content: "";
      width: 0;
      height: 0;
      background-color: #fff;
      border-radius: 30px;
      transition: width 0.4s;
      z-index: -1;
    }

    &:hover::after {
      width: 80%;
      height: 100%;
    }

    &:hover {
      cursor: pointer;
      color: black;
      transition: 0.5s;
    }
  }

  &__picture {
    position: relative;
    
    width: 64px;
    height: 64px;
    margin-top: 15px;
    margin-right: 32px;

    background: #fff;
    border-radius: 30px;
  }

  &__icon {
    position: absolute;
    top: calc(50% - 12px);
    left: calc(50% - 12px);

    width: 25px;
    height: 25px;

  }

  &__text {
    width: 250px;
    margin: 5px;

    font-family: "Open Sans", Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
  }

  &__photo {
    position: absolute;
    border-radius: 30px 0 0;
    top: 30%;
    right: 0;
    width: 40vw;
    height: 70%;
    background: url(../images/bridge.png) center center/cover no-repeat;
  }
}
